@import url("https:fonts.googleapis.com/css?family=Encode+Sans+Expanded");

html,
body {
  font-family: "Encode Sans Expanded", sans-serif;
  height: 100%;
  width: 100vw;
  margin: 0;
}

p {
  margin-bottom: 1rem;
}

.counter {
  padding: 10px;
}
.counter .counter-controls {
  display: inline-block;
  margin-left: 10px;
}
.counter .counter-controls .button {
  margin-right: 4px;
}

ol.instructions {
  list-style-type: decimal;
  margin-left: 1rem;
  margin-top: 0.5rem;
}

ol.instructions li {
  margin-bottom: 1rem;
}

ol.instructions li ol {
  margin-top: 0.5rem;
  margin-left: 2.5rem;
  list-style-type: decimal;
}
